<script>
  export default {
    data() {
      return {
        tableData: [],
        missingTable: [],

         ruleForm: {
          loseName: '',
          loseIphone: '',
          loseSex: '男',
          loseAddress: '',
          description: '',
          imgUrl: ''
        },
        page: {
          pageIndex: '1',
          pageSize:  '10'
        },

        dialogVisible: false,
        missingVisible: false,
      }
    },
    created() {
      this.getDataList()
    },
    methods: {

       async getDataList() {
        const data = await this.$api.threadList(this.page);
        this.tableData = data;
      },
      async onSubmit(){
        const result = await this.$api.addSearch(this.ruleForm);
        if(!result){
          return false;
        }
        this.getDataList()
        this.dialogVisible = false
        this.clearForm();
      },
      uploadSuccess(imgUrl) {
          
         this.ruleForm.imgUrl = imgUrl;    
      },
      clearForm(){
        this.ruleForm.loseName = '';
        this.ruleForm.loseIpone = '';
        this.ruleForm.loseSex = '男';
        this.ruleForm.loseAddress = '';
        this.ruleForm.description = '';
        this.ruleForm.imgUrl = '';
      },
      viewMatch(row){
        this.missingTable = [];
        if(row.missingperson != null){
          this.missingTable.push(row.missingperson)
        }
        this.missingVisible = true
      }
    }
  }
</script>

